@charset "UTF-8";
body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 16px;
}

a {
  text-decoration: none;
  color: #333333;
}

ul, ol {
  list-style: none;
}

img {
  vertical-align: top;
  border: none;
}

body {
  font-family: "roboto", "Noto Sans KR", sans-serif;
  color: #333333;
  overflow: hidden;
}
body.fixed {
  overflow-y: hidden;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

::-webkit-scrollbar {
  width: 5px;
  height: 0;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(#4096EE, #39CED6);
}

::-webkit-scrollbar-track {
  background-color: rgba(57, 206, 214, 0.1490196078);
}

.video {
  height: 100vh;
  overflow: hidden;
}

#main_video {
  display: block;
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
     object-fit: cover;
}

#header .header_top {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
#header .header_top .logo {
  position: fixed;
  left: 30px;
  top: 30px;
  z-index: 100;
}
#header .header_top .logo h1 a {
  display: block;
  line-height: 30px;
  font-size: 30px;
  font-weight: 900;
  text-shadow: 1px 1px 2px #999;
  color: #fff;
}
#header .top_right {
  position: fixed;
  right: 30px;
  top: 30px;
  z-index: 20;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}
#header .top_right #gnb ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  font-size: 0;
}
#header .top_right #gnb ul li a {
  display: inline-block;
  font-size: 15px;
  font-weight: 300;
  line-height: 30px;
  color: #fff;
  margin-left: 50px;
  position: relative;
}
#header .top_right #gnb ul li.active a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #fff;
}
#header .top_right .tab {
  display: none;
  position: relative;
  width: 25px;
  height: 20px;
  margin-left: 30px;
  margin-bottom: 0;
  box-sizing: border-box;
  z-index: 2000;
}
#header .top_right .tab span {
  position: absolute;
  right: 0;
  top: 0;
  height: 3px;
  background-color: #fff;
  transition: 0.5s;
  border-radius: 3px;
  box-sizing: border-box;
}
#header .top_right .tab span:nth-child(1) {
  width: 25px;
}
#header .top_right .tab span:nth-child(2) {
  top: 8px;
  width: 25px;
}
#header .top_right .tab span:nth-child(3) {
  top: 16px;
  width: 25px;
}
#header .top_right .tab.active span:nth-child(1) {
  top: 8px;
  width: 25px;
  transform: rotate(45deg);
}
#header .top_right .tab.active span:nth-child(2) {
  display: none;
}
#header .top_right .tab.active span:nth-child(3) {
  top: 8px;
  width: 25px;
  transform: rotate(-45deg);
}
#header .ctrN {
  position: fixed;
  left: 15px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 20;
}
#header .ctrN ul {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  width: 30px;
  height: 200px;
  position: relative;
  border-radius: 15px;
  overflow: hidden;
}
#header .ctrN ul li a {
  display: block;
  width: 11px;
  height: 11px;
  background-color: rgba(255, 255, 255, 0.3);
  text-indent: -9999px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
}
#header .ctrN ul li.active a::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 11px;
  height: 11px;
  background-color: #fff;
  opacity: 1;
}
#header .link {
  position: fixed;
  right: 2px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 200;
}
#header .link ul {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 30px;
  height: 92px;
}
#header .link ul li a {
  display: block;
  width: 30px;
  height: 30px;
  font-size: 26px;
  line-height: 30px;
  text-align: center;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  opacity: 0.8;
}
#header .link ul li a:hover {
  opacity: 1;
  transform: scale(1.1);
}
#header .contact {
  position: fixed;
  right: 30px;
  bottom: 55px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  width: 75px;
  height: 24px;
  z-index: 30;
  border: 1px solid #fff;
  box-sizing: border-box;
}
#header .contact a {
  display: block;
  line-height: 22px;
  font-size: 14px;
  font-weight: 100;
  color: #fff;
  letter-spacing: 1px;
}
#header .top {
  position: fixed;
  right: 30px;
  bottom: 80px;
  z-index: 30;
}
#header .top a {
  display: none;
  line-height: 22px;
  padding: 0 10px;
  font-size: 14px;
  width: 75px;
  box-sizing: border-box;
  font-weight: 100;
  color: #fff;
  letter-spacing: 1px;
  border: 1px solid #fff;
  text-align: center;
}
#header .download {
  position: fixed;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  right: 30px;
  bottom: 30px;
  width: 75px;
  height: 24px;
  z-index: 30;
  border: 1px solid #fff;
  opacity: 1;
  box-sizing: border-box;
}
#header .download a {
  color: #fff;
  line-height: 22px;
  font-size: 14px;
  font-weight: 100;
  letter-spacing: 1px;
}
#header .download a:last-child {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
}
#header .pagedown {
  position: absolute;
  left: 50%;
  bottom: 3%;
  text-align: center;
  transform: translateX(-50%);
  opacity: 1;
  transition: opacity 0.4s;
  z-index: 20;
}
#header .pagedown.active {
  opacity: 0;
}
#header .pagedown a {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 40px;
  overflow: hidden;
  text-indent: -9999px;
  border: 1px solid #fff;
  border-radius: 20px;
}
#header .pagedown a::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 15px;
  margin-left: -3px;
  width: 6px;
  height: 6px;
  background-color: #fff;
  border-radius: 50%;
  -webkit-animation: pagedown 1s infinite;
          animation: pagedown 1s infinite;
}
#header .pagedown span {
  display: block;
  font-size: 15px;
  color: #fff;
  font-weight: 100;
}
#header .hcopy {
  position: absolute;
  left: 5px;
  bottom: 5px;
  z-index: 100;
  color: #fff;
  font-size: 11px;
  font-weight: 100;
}
#header.active .logo h1 a {
  text-shadow: none;
  color: transparent;
  background: linear-gradient(90deg, #4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
}
#header.active #gnb ul li a {
  font-weight: 500;
  color: transparent;
  background: linear-gradient(90deg, #4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
}
#header.active #gnb ul li.active a::after {
  background: linear-gradient(90deg, #4096EE, #39CED6);
}
#header.active .header_top .top_right .tab span {
  background: linear-gradient(90deg, #4096EE, #39CED6);
}
#header.active .ctrN ul li a {
  background-color: rgba(0, 0, 0, 0.1);
}
#header.active .ctrN ul li.active a::after {
  background: linear-gradient(#4096EE, #39CED6);
}
#header.active .link {
  right: 7px;
}
#header.active .link ul li a {
  color: transparent;
  border: 1px solid transparent;
  font-weight: 100;
  background-image: linear-gradient(#4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
  -o-border-image: linear-gradient(#4096EE, #39CED6);
     border-image: linear-gradient(#4096EE, #39CED6);
  border-image-slice: 1;
  overflow: hidden;
}
#header.active .contact {
  -o-border-image: linear-gradient(#4096EE, #39CED6);
     border-image: linear-gradient(#4096EE, #39CED6);
  border-image-slice: 1;
  overflow: hidden;
}
#header.active .contact a {
  color: transparent;
  background-image: linear-gradient(#4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
  box-sizing: border-box;
  -o-border-image: linear-gradient(#4096EE, #39CED6);
     border-image: linear-gradient(#4096EE, #39CED6);
  border-image-slice: 1;
  overflow: hidden;
  font-weight: 500;
}
#header.active .top a {
  display: block;
  color: transparent;
  background-image: linear-gradient(#4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
  -o-border-image: linear-gradient(#4096EE, #39CED6);
     border-image: linear-gradient(#4096EE, #39CED6);
  border-image-slice: 1;
  overflow: hidden;
  font-weight: 500;
}
#header.active .download {
  -o-border-image: linear-gradient(#4096EE, #39CED6);
     border-image: linear-gradient(#4096EE, #39CED6);
  border-image-slice: 1;
  overflow: hidden;
}
#header.active .download a {
  color: transparent;
  background-image: linear-gradient(#4096EE, #39CED6);
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 500;
}

@-webkit-keyframes pagedown {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes pagedown {
  0% {
    transform: translateY(-10px);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}
