/* 휠 영역 */
.m_dim {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(#4096EE, #39CED6);
  z-index: 1;
  opacity: 0.7;
}

.m_cont {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90vw;
  height: 75vh;
  z-index: 2;
  background-color: transparent;
  overflow-y: hidden;
}
.m_cont .m_cont_wheel {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 760px) {
  .m_cont {
    overflow-y: auto;
    width: 95%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 75vh;
    z-index: 2;
    background-color: transparent;
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  body {
    overflow: auto;
  }
  ::-webkit-scrollbar {
    width: 1px;
  }
  .m_cont::-webkit-scrollbar {
    display: none;
  }
}
